import { Component } from '@angular/core';
import { stripSummaryForJitNameSuffix } from '@angular/compiler/src/aot/util';
import { NumberFormatStyle } from '@angular/common';

@Component({
    selector: 'app-server',
    templateUrl: './server.component.html'
})

export class ServerComponent {
    allowNewServer = false;
    serverCreationStatus = 'No servers created';
    flag = 1;
    serverName = 'aaa';

    getNumber(numero:number){
        if ( numero == 1) {
            return 'ligado'
        }
        else {
            return 'desligado'
        }
    }

    constructor(){
        setTimeout(() =>{
            this.allowNewServer = true;
        },2000);
    }

    onCreateServer(){
        this.serverCreationStatus = 'Server was created' + ' ' + this.flag + ' ' + this.serverName;
        this.flag = this.flag + 1;
    }

    onUpdateServerName(event: any){
        this.serverName = (<HTMLInputElement>event.target).value;
    }
};